import { convertArrayToFiles } from './convertArrayToFiles';
import { CSVToArray } from './parseCSV';

(() => {
  const runButton = document.getElementById('run') as HTMLButtonElement;
  const inputTextarea = document.getElementById(
    'inputtext'
  ) as HTMLTextAreaElement;
  const resultTextarea = document.getElementById(
    'result'
  ) as HTMLTextAreaElement;

  runButton.addEventListener('click', () => {
    const t = inputTextarea.value;
    resultTextarea.value = convertArrayToFiles(CSVToArray(t)).join(
      '\n\n\n----------------------------\n\n\n'
    );
  });
})();
