import { convertNew } from './main';
import { isEmptyRow } from './lib/isEmptyRow';
import { RowNew, convertRowNew } from './RowNew';
export const HEADER_ROW_COUNT = 2;

export const convertArrayToFiles = (array: string[][]): string[] => {
  // FIXME: 決め打ちなので直す
  const headerIndex = 3; // array.findIndex((row) => row[0] === 'ID');

  const newContentsArray: RowNew[] = array
    .slice(headerIndex + HEADER_ROW_COUNT) // ヘッダー行以降を取得
    .filter((r) => !isEmptyRow(r)) // 空行消す
    .map(convertRowNew);

  const files = convertNew(newContentsArray);
  return files;
};
