import type { Style, TextUpdateStatus } from './main';
import { hasChinese } from './lib/hasChinese';

export type RowNewJapaneseEnglish = {
  id: number;
  status: TextUpdateStatus | null;
  style: Style;
  japanese: string;
  english: string;
  comment: string;
};
export type RowNewWithChinese = {
  chineseHans: string;
  chineseHant: string;
} & RowNewJapaneseEnglish;
export function isRowNewWithChinese(row: RowNew): row is RowNewWithChinese {
  return 'chineseHans' in row;
}
export function convertRowNew(row: string[]): RowNew {
  if (hasChinese(row)) {
    return {
      id: parseInt(row[0], 10),
      status: ((row[1] || row[7]) as TextUpdateStatus) || null,
      style: row[2] as Style,
      japanese: row[3],
      chineseHans: row[4],
      chineseHant: row[5],
      english: row[6],
      comment: row[9],
    };
  }
  return {
    id: parseInt(row[0], 10),
    status: ((row[1] || row[5]) as TextUpdateStatus) || null,
    style: row[2] as Style,
    japanese: row[3],
    english: row[4],
    comment: row[7],
  };
}
export type RowNew = RowNewJapaneseEnglish | RowNewWithChinese;
