import { RowNew, isRowNewWithChinese } from './RowNew';

export type TextUpdateStatus = '追加' | '削除' | '変更' | '非表示' | '移動';
export type SpecialStyle =
  | '[段落スタイル]'
  | 'Pct!'
  | 'Pct'
  | 'Nt!'
  | 'Nt'
  | 'Ct'
  | '[文字以外の構成要素]'
  | 'Btn+'
  | 'Btn-'
  | 'hImg'
  | 'wTab'
  | 'wTbs'
  | 'wTbe'
  | 'tTb1'
  | 'tTb2'
  | 'tTb3'
  | 'tTb4'
  | 'Tab'
  | 'Wdgt'
  | 'Spcl'
  | '[トップページ専用]'
  | 'hmH1'
  | 'hmH2'
  | 'hmH3'
  | 'aBar'
  | 'tImg'
  | 'nwH4'
  | 'nwP'
  | '[float属性]'
  | 'fLH3'
  | 'fLH4'
  | 'fLP'
  | 'fLImg'
  | 'fRH3'
  | 'fRH4'
  | 'fRP'
  | 'fRImg'
  | '[ストコン専用]'
  | 'scH2'
  | 'scH3'
  | 'scH4'
  | 'scH1'
  | 'scP';
type HTMLStyle = 'H1' | 'H2' | 'H3' | 'H4' | 'P' | 'UL' | 'OL' | 'Img';

export type Style = SpecialStyle | HTMLStyle;
export type RowOld = [
  id: number,
  status: TextUpdateStatus,
  style: Style,
  japanese: string,
  english: string,
  comment: string,
];
export type RowWithChinese = [
  id: number,
  status: TextUpdateStatus,
  style: Style,
  japanese: string,
  chineseHans: string,
  chineseHant: string,
  english: string,
  comment: string,
];
type Language = 'japanese' | 'english';

const getHeaderAndFooter = (
  style: Style,
  language: Language
): [header: string, footer: string] => {
  switch (style) {
    case 'H2':
    case 'H3':
    case 'H4':
      return [`${'#'.repeat(parseInt(style.charAt(1), 10))} `, ''];
    case 'Img':
      return ['![image](', ')'];
    case 'Nt!':
    case 'Pct!':
      if (language === 'japanese') {
        return ['**※', '**'];
      }
      return ['**Note: ', '**'];
    case 'Nt':
    case 'Ct':
      if (language === 'japanese') {
        return ['*※', '*'];
      }
      return ['*Note: ', '*'];
    case 'Pct':
      if (language === 'japanese') {
        return ['※', ''];
      }
      return ['Note: ', ''];
    case 'Btn-':
      return ['<Button name="', '" disabled />'];
    case 'Btn+':
      return ['<Button name="', '" />'];
    case 'wTab':
      return ['<ContentTabs>\n', '</ContentTabs>'];
  }
  return ['', ''];
};

const format = (text: string, style: Style): string => {
  let prevText = text;
  do {
    prevText = text;
    text = text.replace(
      /\[(.+)\]\(https?:\/\/(www\.)?jmof\.jp(\/.+)\)/,
      '[$1]($3)'
    );
  } while (text !== prevText);
  switch (style) {
    case 'UL':
      return `- ${text}`;
    case 'OL':
      return `1. ${text}`;
    case 'Nt!':
    case 'Nt':
    case 'Pct!':
    case 'Pct':
    case 'Ct':
      const trimTextMatch = text.match(/^(Note: |※)/);
      if (trimTextMatch !== null) {
        const len = trimTextMatch?.[0]?.length;
        if (len != null) {
          return text.slice(len);
        }
      }
      return text;
    case 'Btn+':
      const link = text.match(/\[(.+)\]\((.+)\)/);
      if (link === null) {
        return text;
      }
      return `${link[1]}" to="${link[2]}`;
    default:
      return text;
  }
};

const convertRow = (
  row: RowOld,
  index: number,
  csv: RowOld[]
): { japaneseText: null | string; englishText: null | string } => {
  const [_id, status, style, japanese, english, comment] = row;
  if (['非表示', '削除'].includes(status) || ['Spcl', 'hImg'].includes(style)) {
    return null;
  }

  // wip
  if (
    ![
      'H2',
      'H3',
      'H4',
      'P',
      'UL',
      'OL',
      'Img',
      'Nt',
      'Nt!',
      'Ct',
      'Pct!',
      'Pct',
      'Btn-',
      'Btn+',
      'wTab',
    ].includes(style)
  ) {
    return null;
  }

  const japaneseText = convertEachLanguage(style, japanese, 'japanese');
  const englishText = convertEachLanguage(style, english, 'english');
  return {
    japaneseText,
    englishText,
  };
};

const convertEachLanguage = (
  style: Style,
  text: string,
  language: 'japanese' | 'english'
): null | string => {
  if (text === '') {
    return null;
  }
  const [japaneseHeader, japaneseFooter] = getHeaderAndFooter(style, language);
  return `${japaneseHeader}${format(text, style)}${japaneseFooter}`;
};
const styleGen = (
  s: TemplateStringsArray,
  _?: 0
): ((t: string[]) => string[]) => {
  return typeof _ === 'undefined'
    ? (args) => [...args].fill(s.join())
    : (args: string[]) => args.map((v) => `${s[0]}${v}${s[1]}`);
};
export const addRowStyle = (
  text: string[],
  style: Style,
  addHeader: (key: string, value: string[]) => void,
  row: RowNew
): string[] => {
  const styleFn = new Map<Style, (t: string[]) => string[] | null>([
    [
      'H1',
      (t) => {
        addHeader('title', t);
        return null;
      },
    ],
    [
      'hImg',
      (_) => {
        addHeader('titleImage', [..._].fill(row.comment.replace('\n', ' ')));
        return null;
      },
    ],
    [
      'H2',
      styleGen`
## ${0}`,
    ],
    [
      'H3',
      styleGen`
### ${0}`,
    ],
    [
      'H4',
      styleGen`
#### ${0}`,
    ],
    ['P', styleGen`${0}`],
    ['UL', styleGen`- ${0}`],
    ['OL', styleGen`1. ${0}`],
    ['Img', styleGen`![image](${0})`],
    [
      'Nt!',
      (t) =>
        t.map((v, i) => {
          const trimTextMatch = v.match(/^(Note: |※)/);
          if (trimTextMatch !== null) {
            v = v.slice(trimTextMatch[0].length);
          }
          if (i === t.length - 1) {
            return `**Note: ${v}**`;
          }
          return `**※${v}**`;
        }),
    ],
    [
      'Pct!',
      (t) =>
        t.map((v, i) => {
          const trimTextMatch = v.match(/^(Note: |※)/);
          if (trimTextMatch !== null) {
            v = v.slice(trimTextMatch[0].length);
          }
          if (i === t.length - 1) {
            return `**Note: ${v}**`;
          }
          return `**※${v}**`;
        }),
    ],
    [
      'Nt',
      (t) =>
        t.map((v, i) => {
          const trimTextMatch = v.match(/^(Note: |※)/);
          if (trimTextMatch !== null) {
            v = v.slice(trimTextMatch[0].length);
          }
          if (i === t.length - 1) {
            return `*Note: ${v}*`;
          }
          return `*※${v}*`;
        }),
    ],
    [
      'Ct',
      (t) =>
        t.map((v, i) => {
          const trimTextMatch = v.match(/^(Note: |※)/);
          if (trimTextMatch !== null) {
            v = v.slice(trimTextMatch[0].length);
          }
          if (i === t.length - 1) {
            return `*Note: ${v}*`;
          }
          return `*※${v}*`;
        }),
    ],
    [
      'Pct',
      (t) =>
        t.map((v, i) => {
          const trimTextMatch = v.match(/^(Note: |※)/);
          if (trimTextMatch !== null) {
            v = v.slice(trimTextMatch[0].length);
          }
          if (i === t.length - 1) {
            return `Note: ${v}`;
          }
          return `※${v}`;
        }),
    ],
    [
      'Btn-',
      (t) =>
        t.map((v) => {
          const link = v.match(/\[(.+)\]\((.+)\)/);
          if (link === null) {
            return `<Button name="${v}" disabled />`;
          }
          return `<Button name="${link[1]}" to="${link[2]}" disabled />`;
        }),
    ],
    [
      'Btn+',
      (t) =>
        t.map((v) => {
          const link = v.match(/\[(.+)\]\((.+)\)/);
          if (link === null) {
            return `<Button name="${v}" />`;
          }
          return `<Button name="${link[1]}" to="${link[2]}" />`;
        }),
    ],
    ['wTbs', styleGen`\n<ContentTabs>`],
    ['wTbe', styleGen`</ContentTabs>\n`],
    [
      'tTb1',
      styleGen`  <ContentTab
    name="${0}"
    tabId="__FIXME_TABID_GOES_HERE__"
    defaultChekcedPath={["/__FIXME__/PATH/NAME/HERE"]}
  >
`,
    ],
    [
      'tTb2',
      styleGen`
  </ContentTab>
  <ContentTab
    name="${0}"
    tabId="__FIXME_TABID_GOES_HERE__"
    defaultChekcedPath={["/__FIXME__/PATH/NAME/HERE"]}
  >
`,
    ],
    [
      'tTb3',
      styleGen`
  </ContentTab>
  <ContentTab
    name="${0}"
    tabId="__FIXME_TABID_GOES_HERE__"
    defaultChekcedPath={["/__FIXME__/PATH/NAME/HERE"]}
  >
`,
    ],
    [
      'tTb4',
      styleGen`  </ContentTab>
  <ContentTab
    name="${0}"
    tabId="__FIXME_TABID_GOES_HERE__"
    defaultChekcedPath={["/__FIXME__/PATH/NAME/HERE"]}
  >`,
    ],
  ]).get(style);
  if (!styleFn) {
    console.warn(`style not found: ${style}`);
    return text;
  }
  return styleFn(text) ?? text;
};
export const convertRowNew = (
  row: RowNew,
  addHeader: (k: string, v: string[]) => void
): string[] =>
  addRowStyle(
    isRowNewWithChinese(row)
      ? [row.japanese, row.chineseHans, row.chineseHant, row.english]
      : [row.japanese, row.english],
    row.style,
    addHeader,
    row
  );
const convertYaml = (k: string, v: string[]): string[] =>
  v.map((v2) => `${k}: "${v2}"`);
const convertYamlArray = (k: string, v: string[][]): string[] | null => {
  if (v.length === 0) {
    return null;
  }
  return v[0].map(
    (_, i) =>
      `${k}:
${v.map((item) => `  - "${item[i].replace('"', '\\"')}"`).join('\n')}`
  );
};
export const headerGenerator = (): [
  generateHeader: () => string[],
  addHeader: (key: string, value: string[]) => void,
] => {
  const header: {
    slug: null | string[];
    title: null | string[];
    slugs: string[][];
    titleImage: null | string[];
    [key: string]: string[] | string[][] | null;
  } = {
    slug: null,
    title: null,
    slugs: [],
    titleImage: null,
  };
  const headerNotEmpty = (k: string) =>
    header[k] != null && header[k] instanceof Array && header[k].length !== 0;
  const headerArrayNotEmpty = (k: string) =>
    header[k] instanceof Array && header[k].length > 0;
  const addHeader = (key: string, value: string[]) => {
    switch (key) {
      case 'slugs':
        header[key].push(value);
        return;
      case 'slug':
      case 'title':
      case 'titleImage':
      default:
        header[key] = value;
        return;
    }
  };
  const generateHeader = (): string[] => {
    const keys = Object.keys(header).filter(
      (k) => !['slug', 'title', 'slugs', 'titleImage'].includes(k)
    );
    const contents = [
      ...(['slug', 'title'] as const)
        .filter(headerNotEmpty)
        .map((k) => convertYaml(k, header[k])),
      ...(['slugs'] as const)
        .filter(headerArrayNotEmpty)
        .map((k) => convertYamlArray(k, header[k])),
      ...(['titleImage'] as const)
        .filter(headerNotEmpty)
        .map((k) => convertYaml(k, header[k])),
      ...keys
        .map((k): null | string[] => {
          if (header[k] instanceof Array) {
            if (header[k].length === 0) {
              return null;
            }
            if (header[k] instanceof Array) {
              if (header[k][0].length === 0) {
                return null;
              }
              // Array
              return convertYamlArray(k, header[k] as string[][]);
            }
            return convertYaml(k, header[k] as string[]);
          }
          return null;
        })
        .filter(Boolean),
    ];
    const cleanContents = contents
      .reduce(
        (prev, curr) => prev.map((v, i) => [...v, curr[i]]),
        [...contents?.[0]].map(() => []) as string[][]
      )
      .map((v) => v.join('\n'));
    return cleanContents.map(
      (c) => `---
${c}
---`
    );
  };
  return [generateHeader, addHeader];
};

export const convertNew = (csv: RowNew[]): string[] => {
  const [generateHeader, addHeader] = headerGenerator();
  const convertedRows = csv
    .filter((r) => r.status !== '削除' && r.status !== '非表示')
    .map((r) => convertRowNew(r, addHeader));
  const joinedText = convertedRows
    .reduce(
      (prev: string[][], convertedRow) => {
        convertedRow.map((v, i) => prev[i].push(v));
        return prev;
      },
      [...convertedRows[0]].map((): string[] => [])
    )
    .map((v) => v.filter(Boolean).join('\n'));
  const headers = generateHeader();
  const result = joinedText.map((v, i) => {
    if (headers[i] === undefined) {
      return `${headers[headers.length - 1]}
${v}`;
    }
    return `${headers[i]}
${v}`;
  });

  return result;
};
export const convert = (
  csv: RowOld[]
): [japaneseText: string, englishText: string] => {
  const tabRow = csv.find(([, , style]) => style === 'Tab');
  const tabText: [string, string] = [tabRow?.[3] ?? '', tabRow?.[4] ?? ''];
  const titleRow = csv.find(([, , style]) => style === 'H1');
  const titleText: [string, string] = [
    titleRow?.[3] ?? '',
    titleRow?.[4] ?? '',
  ];
  const headerImageRow = csv.find(([, , style]) => style === 'hImg');
  const headerImage =
    (headerImageRow?.[5] ?? '').match(
      /https?:\/\/[\w!\?/\+\-_~=;\.,\*&@#\$%\(\)'\[\]]+/
    )?.[0] ?? '';
  const getHeaderText = (language_key: 0 | 1) => `---
slug: "/url/input/here"
title: "${titleText[language_key]}"
${
  headerImageRow
    ? `titleImage: "${headerImage}"
`
    : ''
}---${
    tabText[language_key] === ''
      ? ''
      : `

  <ContentTab
    name="${tabText[language_key]}"
    tabId="tabid_goes_here"
    defaultCheckedPath={["/path/name/here"]}
  >`
  }
`;
  const getFooterText = (language_key: 0 | 1) =>
    tabText[language_key] === ''
      ? ''
      : `

  </ContentTabs>`;
  const result = csv.map(convertRow).filter(Boolean);
  const japaneseResult = result
    .map((v) => v.japaneseText)
    .filter(Boolean) // 空文字とnullを除外
    .reduce(joinText);
  const englishResult = result
    .map((v) => v.englishText)
    .filter(Boolean)
    .reduce(joinText);
  return [japaneseResult, englishResult].map(
    (resultText, i: 0 | 1) =>
      `${getHeaderText(i)}${resultText}${getFooterText(i)}
`
  ) as [string, string];
};

const joinText = (
  text: string,
  row: string,
  index: number,
  array: string[]
) => {
  const prev = index > 0 ? array[index - 1] : '';
  if (
    // 前の段落がリスト
    ('- ' === prev.slice(0, 2) && '- ' === row.slice(0, 2)) ||
    // 前の段落が番号つきリスト
    ('1. ' === prev.slice(0, 3) && '1. ' === row.slice(0, 3)) ||
    // 前の段落が見出し
    prev.match(/^#{2,4} ./) !== null
  ) {
    // 間に1行改行を入れる
    return `${text}\n${row}`;
  }
  return `${text}\n\n${row}`;
};

const parseTitle = (text: string): string => {
  const texts = text.split('_');
  return texts[texts.length - 1];
};

export const convertNews = (
  csv: RowOld[]
): null | { ja: string; en: string } => {
  let day = {
    ja: '',
    en: '',
  };
  const result = csv.map((row) => {
    if (row[2] === 'nwH4') {
      day = {
        ja: row[3],
        en: row[4],
      };
      return null;
    } else if (row[2] === 'nwP') {
      return {
        ja: { [day.ja]: row[3] },
        en: { [day.en]: row[4] },
      };
    }
    return null;
  });
  const filteredResult = result.filter(Boolean);
  if (filteredResult.length === 0) {
    return null;
  }
  const jaResult = filteredResult.map(({ ja }) => ja);
  const enResult = filteredResult.map(({ en }) => en);
  return {
    ja: JSON.stringify(jaResult),
    en: JSON.stringify(enResult),
  };
};

export const checkHasChinese = (csv: string[][]): boolean =>
  csv[3].some((v) => v.startsWith('中文'));
